.navbar {
    background-color: #f8f8f8;
    height: 60px;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.nav-item > span{
    cursor: pointer;
}
.navbar-container {
    display: flex;
    align-items: center;
}

.navbar-logo {
    font-size: 1.5rem;
    color: #333;
    text-decoration: none;
}

.nav-menu {
    display: flex;
    list-style: none;
}

.nav-item {
    margin-right: 20px;
}

.nav-links {
    font-size: 1rem;
    color: #555;
    text-decoration: none;
    transition: color 0.3s ease;
}

.nav-links:hover {
    color: #d1434a;
}

.active-link {
    color: #d1434a;
    font-weight: bold;
}

.dropdown {
    position: relative;
}

.dropdown-content {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    display: none;
    padding: 10px;
}

.dropdown:hover .dropdown-content {
    display: block;
}
